    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.7);
}

.modal-box {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgb(255, 255, 255);
    width: 45%;
    height: 60%;
    box-shadow: 12px 22px 8px rgba(0, 0, 0, 0.8);
    overflow-y: auto;
